import styled from "styled-components"
import { useState } from "react";

import userEx from "./user.png";
import Profile from "./Profile"
import Introduce from "./Introduce";
import PostList from "./PostList";
import Sort from "./sort";
import { useNavigate } from "react-router-dom";

const IndividualDiv = styled.div`
    margin : 5rem;
`
const ProfileIntroduceDiv = styled.div`
    display: flex;
    margin-bottom: 2rem;
`
const PostListDiv = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
`

function Individual() {
    const navi = useNavigate();
    const [toggle, setToggle] = useState(false);
    const categoryList = [
        '좋아요',
        '댓글',
        '공유'
    ]
    const [category, setCategory] = useState(categoryList[0]);
    const testuserdata = {
        name: "무한이",
        intro: "23살 프로여행러 여행기록과 일상을 공유합니다.",
        postcount: 29,
        follower: 1509,
        following: 23
    }
    let listdata = [
        {
            img: 'data:image/jpeg;base64,',
            like: 204,
            comment: 14,
            share: 49
        },
        {
            img: 'data:image/jpeg;base64,',
            like: 159,
            comment: 12,
            share: 32
        },
        {
            img: 'data:image/jpeg;base64,',
            like: 555,
            comment: 49,
            share: 101
        },
        {
            img: 'data:image/jpeg;base64,',
            like: 111,
            comment: 11,
            share: 22
        },
        {
            img: 'data:image/jpeg;base64,',
            like: 222,
            comment: 22,
            share: 33
        }
    ]
    const categorySort = ()=>{
        if(category==='좋아요'){
            listdata = listdata.sort((a,b)=>(b.like-a.like))
        }
        else if(category === '댓글'){
            listdata = listdata.sort((a,b)=>(b.comment-a.comment))
        }
        else if(category === '공유'){
        listdata = listdata.sort((a,b)=>(b.share-a.share))
        }
    }
    return (
        <IndividualDiv onClick={() => { toggle && setToggle(!toggle) }}>
            <ProfileIntroduceDiv>
                <Profile src={userEx} />
                <Introduce
                    name={testuserdata.name}
                    intro={testuserdata.intro}
                    postcount={testuserdata.postcount}
                    follower={testuserdata.follower}
                    following={testuserdata.following}
                />
            </ProfileIntroduceDiv>
            <Sort toggle={toggle} setToggle={setToggle} category={category} setCategory={setCategory} categoryList={categoryList}/>
            <PostListDiv onClick={()=>{navi("/managepost")}}>
                {
                    categorySort()
                }
                {
                        listdata.map((listdata,id) => {
                            return <PostList key={id} like={listdata.like} comment={listdata.comment} share={listdata.share} img={listdata.img} />
                        })
                }
            </PostListDiv>
        </IndividualDiv>
    )
}

export default Individual;